/* App-specific styles */
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 2;
}

main {
  flex: 1;
}

/* Section spacing */
section {
  position: relative;
  z-index: 2;
}

/* Enhanced background patterns */
.bg-pattern {
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(201, 169, 110, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(201, 169, 110, 0.08) 0%, transparent 50%);
}

/* Smooth transitions between sections */
section {
  transition: all 0.3s ease;
}

/* Enhanced visual hierarchy */
.App {
  backdrop-filter: blur(0.5px);
}

/* Authentic Italian Cafe Color Scheme - Light Theme */
:root {
  --primary-gold: #c9a96e;
  --dark-gold: #a67c52;
  --light-gold: #e6d7b8;
  --warm-white: #faf8f5;
  --cream: #f5f1eb;
  --soft-gray: #f8f6f3;
  --text-dark: #2d2d2d;
  --text-medium: #4a4a4a;
  --text-light: #6b6b6b;
  --accent-red: #8b0000;
  --accent-green: #2d5016;
  --shadow-light: rgba(201, 169, 110, 0.15);
  --shadow-medium: rgba(201, 169, 110, 0.25);
  --shadow-dark: rgba(0, 0, 0, 0.1);
  
  /* Background colors */
  --bg-primary: #faf8f5;
  --bg-secondary: #f5f1eb;
  --bg-tertiary: #f8f6f3;
  --bg-card: #ffffff;
  --bg-overlay: rgba(255, 255, 255, 0.95);
}

/* Dark Theme - Stunning Night Mode */
[data-theme="dark"] {
  --primary-gold: #d4af37;
  --dark-gold: #b8941f;
  --light-gold: #f4e4a6;
  --warm-white: #1a1a1a;
  --cream: #2a2a2a;
  --soft-gray: #333333;
  --text-dark: #e0e0e0;
  --text-medium: #c0c0c0;
  --text-light: #a0a0a0;
  --accent-red: #ff6b6b;
  --accent-green: #51cf66;
  --shadow-light: rgba(212, 175, 55, 0.2);
  --shadow-medium: rgba(212, 175, 55, 0.3);
  --shadow-dark: rgba(0, 0, 0, 0.3);
  
  /* Dark background colors */
  --bg-primary: #1a1a1a;
  --bg-secondary: #2a2a2a;
  --bg-tertiary: #333333;
  --bg-card: #2d2d2d;
  --bg-overlay: rgba(26, 26, 26, 0.95);
}
