.pattern {
  position: relative;
  display: inline-block;
}

.cornerPattern {
  position: relative;
  width: 40px;
  height: 40px;
}

.cornerPattern .line {
  position: absolute;
  background-color: var(--primary-gold);
  opacity: 0.6;
}

.cornerPattern .line:first-child {
  width: 20px;
  height: 2px;
  top: 0;
  left: 0;
}

.cornerPattern .line:last-child {
  width: 2px;
  height: 20px;
  top: 0;
  left: 0;
}

.dividerPattern {
  display: flex;
  align-items: center;
  gap: 8px;
}

.dot {
  width: 6px;
  height: 6px;
  background-color: var(--primary-gold);
  border-radius: 50%;
  opacity: 0.7;
}

.borderPattern {
  width: 100%;
  height: 1px;
  position: relative;
}

.borderLine {
  width: 100%;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent 0%,
    var(--primary-gold) 20%,
    var(--primary-gold) 80%,
    transparent 100%
  );
  opacity: 0.3;
}

/* Sizes */
.small .cornerPattern {
  width: 20px;
  height: 20px;
}

.small .cornerPattern .line:first-child {
  width: 10px;
}

.small .cornerPattern .line:last-child {
  height: 10px;
}

.small .dot {
  width: 4px;
  height: 4px;
}

.large .cornerPattern {
  width: 60px;
  height: 60px;
}

.large .cornerPattern .line:first-child {
  width: 30px;
}

.large .cornerPattern .line:last-child {
  height: 30px;
}

.large .dot {
  width: 8px;
  height: 8px;
}
